export function areTextsEqual(text1, text2) {
    const normalizeText = (text) => text
        .replace(/’/g, "'")
        .replace(/“/g, '"')
        .replace(/”/g, '"')
        .replace(/«/g, '"')
        .replace(/»/g, '"')
        .replace(/\u00A0/g, ' ')
        .replace(/–|—/g, '-')
    
    return normalizeText(text1) === normalizeText(text2);
}